.mainCon {
    min-height: 86vh;
    height: fit-content;
    /* background-color: rgb(123, 123, 174); */
}

/* .Register{
    background-color: aqua !important;
} */

.registerDiv {
    min-height: 100vh;
    /* background-color: greenyellow; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.registerFormContainer {
    /* background-color: aquamarine; */
    width: 46vw;
    margin-top: 60px;
    padding: 26px 36px;
    background-color: rgba(22, 124, 192, 0.256);
    border-radius: 6px;
    margin: 40px 0px;
}

.photoContainer .btn {
    background-color: #fff;
    transition: all 0.5s ease;
}

.photoContainer .btn:hover {
    background-color: #b0b0b0;
}

.registerFormContainer input {
    padding: 5px 8px;
    width: 100%;
}

.registerFormContainer input {
    margin-bottom: 5px;
}

.registerSubmitBtnCon {
    /* background-color: brown; */
    margin-bottom: 0;
}

.btnCon {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* .registerSubmitBtn{
    background-color: gray;
} */

.registerLink {
    margin-left: 16px;
    text-decoration: none;
    font-style: 22px;
}

/* CreateWorkAdmin */

.workImageCon{
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.imageClass {
    height: 200px;
    width: 150px;
    /* border-radius: 6px; */
    margin-bottom: 16px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.photoC {
    height: 250px;
    width: 250px;
    margin-right: 22px;
    margin-bottom: 16px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
}

.singleWorkDivContainer {
    background-color: #fff;
    margin-bottom: 16px;
    margin-right: 16px;
    height: 290px;
    width: 250px;
    border-radius: 6px;
    /* border-top-right-radius: 6px; */
    position: relative;
    overflow: hidden;
}

.singleWorkDivContainer:hover .workContentContainer {
    transform: translateY(0%);
}

.imgAndTitleContainer {}

.workTitleContainer {
    display: flex;
    justify-content: space-between;
    padding: 6px;
}

.workTitle {
    font-size: 20px;
}

.workContentContainer {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 290px;
    width: 250px;
    z-index: 2;
    background-color: #000000d0;
    color: #fff;
    transform: translateY(100%);
    transition: all 0.4s ease-in;
    padding: 10px 15px;
    overflow-y: auto;
}

.checkoutBtnAndIconsContainer {
    display: flex;
    justify-content: space-between;
    padding: 16px;
    flex-direction: column;
    position: absolute;
    bottom: 5px;
    width: 100%;
    left: 0px;
}

.checkoutBtnAndIconsContainer .btn {
    width: 100%;
}

.workIcons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.workIcons .btn {
    width: 50%;
}

.workIcons .btn:nth-child(1) {
    margin-right: 10px;
}

.del-btn{
    margin-bottom: 10px;
}

