.mainDiv{
    margin-left: 220px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 300px;
    width: 100%;
    margin-top: 20px;
}

.boldifiy{
    font-size: 40px;
        font-weight: bold;
        -webkit-text-stroke: 1px #fff;
        /* text-stroke: 2px black; */
        color: transparent;
        letter-spacing: 3px;
        margin-bottom: 0px;
        font-family: 'Sono', sans-serif;
}


/***************** Skills ******************/
/* CreateSkillAdmin  */
.allSkillsDiv{
    /* background-color: green; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.allSkillsDiv .singleSkillDiv{
    /* background-color: yellow; */
    display: flex;
    justify-content: space-between;
    padding: 0px 16px;
}

.singleSkillDiv > p{
    color: #fff;
}

.deleteIcon{
    color: #fff;
    cursor: pointer;
}

.deleteIcon:hover{
    color: rgb(250, 188, 188);
    cursor: pointer;
}

