/* ******************worksAdmin **************/
.Work{
    background-color: #fff;
}

.workCon{
    /* background-color: gainsboro; */
    width: 100vw;
}

.WorkOuterMostContainer{
    /* background-color: thistle; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0px 60px 36px 60px;
}

.workSingleContainer{
    background-color: #112C62;
    box-shadow: 1px 1px 1.4px 1.6px gray;
    height: 240px;
    width: 250px;
    border-radius: 6px;
    margin: 8px 16px;
    overflow: hidden;
    position: relative;
}

.workDescCon{
    height: 240px;
    width: 250px;
    position: absolute;
    padding: 16px;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.785);
    color: #fff ;
    transform: translateY(100%);
    transition: all 0.5s ease-in;
}

.workSingleContainer:hover .workDescCon{
    transform: translateY(0%);
}

.workImageCon{
    background-color: brown;
    border-radius: 6px;
    /* height: 400px;
    width: 400px; */
}

.workImageCon img{
    height: 200px;
    width: 250px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.workNameCon{
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0px;
    padding: 4px;
}

