.headerOuterMostCon{
    background-color: blueviolet;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 0px;
    /* box-shadow: 1px 1px 1.2px 1.2px rgb(198, 192, 192); */
    box-shadow: 0.6px 0.6px 1px 1px rgb(224, 224, 238);
    z-index: 3;
    /* margin-bottom: 46px; */
}


.navbar-toggler-icon{
    background-color: #fff;
    border-radius: 2px;
}

.headerSecondOuterMostCon{
    background-color: #000;
    /* background-color: greenyellow; */
    padding: 6px 0px;
}

.headerDiv{
    /* background-color: brown; */
    padding: 0px 26px;
}

.headerDiv .logo{
    /* background-color: rebeccapurple; */
    font-family: 'Alkatra', cursive;
    /* font-family: 'Pacifico', cursive; */
}

.headerDiv .logo a{
    /* background-color: aqua; */
    font-size: 36px;
    letter-spacing: 6px;
    margin: 0;
    color: #42B1F8;
    transition: all 0.5s ease-in-out;
}

.headerDiv .logo a:hover{
    color: #6C61F6;
    letter-spacing: 12px;
}

.headerDiv ul{
    /* background-color: blue; */
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.headerDiv ul li{
    /* background-color: aqua; */
    margin: 0px 6px;
}

.headerDiv ul li a{
    color: #fff;
}

/* .headerDiv ul li a > .active{
    color: aliceblue !important;
} */

.headerDiv ul li a:hover{
    color: rgb(87, 151, 208) !important;
}

.headerDiv ul li a:focus{
    color: rgb(87, 151, 208) !important;
}


/* ******************footer *****************/

.Footer{
    background-color: #000;
    color: #fff;
    /* margin-top: 46px; */
}

/* .Footer .footerContent{
    background-color: brown;
} */

.Footer .footerContent p{
    /* background-color: aqua; */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 26px;
    padding: 6px;
    margin: 0;
}
