/* .contactFormDiv{
    background-color: palegoldenrod;
} */

.contactFormDiv > Form{
    margin: 0;
}

.contactFormContainer{
    /* background-color: aquamarine; */
    width: 46vw;
    margin-top: 60px;
    padding: 26px 36px;
    background-color: rgba(22, 124, 192, 0.256);
    border-radius: 6px;
    /* margin: 40px 0px; */
}

#ContactId{
    padding-bottom: 36px;
}

#ContactPageId table{
    width: 100%;
    max-width: 800px;
}

#ContactPageId table tr {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

#ContactPageId table .half td{
    width: 50%;
}

#ContactPageId table tr td {
    width: 100%;
}

#ContactPageId table tr td input {
    width: 100%;
    border: 1px solid #333;
    padding: 5px 8px;
}

#ContactPageId table .half td:nth-child(1) {
    margin-right: 5px;
}

#ContactPageId table .half td input{
    width: 100%;
    border: 1px solid #333;
}

#ContactPageId table tr td {
    width: 100%;
}

#ContactPageId table tr td textarea {
    width: 100%;
    height: 70px;
    border: 1px solid #333;
    padding: 5px 8px;
    min-height: 50px;
}

#ContactPageId table tr td button{
    width: 100%;
    padding: 5px 8px;
    border: none;
    transition: all 0.5s ease;
}

#ContactPageId table tr td button:hover {
    background-color: #7c7b7b;
    color: #fff;
}


@media screen and (max-width: 1000px) {
    .homePageOuterMostCon{
